import styled from "styled-components";
import { Text } from "../../assets/styles/component";

const Ads = styled.div`
  position: relative;
  box-sizing: border-box;

  display: flex;
  flex-direction: column;
  justify-content: flex-end;

  height: 530px;
  width: 520px;
  padding: 42px 32px;

  @media (max-width: 1200px) {
    height: 530px;
    width: 420px;
    padding: 32px;
  }

  text-align: left;

  border-radius: 25px;
  background: ${({ bg_color }) => {
    return bg_color;
  }};
  border: 1px solid #10ace0;
`;

const AdsTitle = styled.h4`
  margin-bottom: 20px;

  font-weight: 400;
  font-size: 36px;
  line-height: 110%;
  color: #ffffff;
`;

const AdsDesc = styled(Text)`
  font-weight: 400;
  font-size: 18px;
  line-height: 150%;
  color: #ffffff;
`;

const AdsImg = styled.img`
  position: absolute;
  opacity: ${({ opacity = 1 }) => opacity};
  max-width: 70%;
  top: 0;
  right: 0;
`;

const AdsType = ({ opacity, adImg, adTitle, description, bg_color }) => {
  return (
    <>
      <Ads bg_color={bg_color}>
        <AdsImg opacity={opacity} loading="lazy" alt="adtype" src={adImg} />
        <AdsTitle>{adTitle}</AdsTitle>
        <AdsDesc>{description}</AdsDesc>
      </Ads>
    </>
  );
};

export default AdsType;
